@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}

@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}
@mixin border-inline-start($value) {
    border-inline-start:$value;
}  
@mixin border-inline-end($value) {
    border-inline-end:$value;
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}

.product-description-head {
	white-space: nowrap;
	overflow: auto;
	width: 100%;
	@media (min-width: 1000px) {
		p {
			margin: 30px 0;
			font-size: 18px;
		}
	}
}
.product__toggle-nav:not(last-child) {
	a {
		@include margin-end(30px);
	}
}
.product__toggle-content {
	position: relative;
	width: 100%;
	overflow: hidden;
	p {
		line-height: 1.7em;
		span {
			color: var(--subtext-color);
		}
	}
	h1, h2, h3, h4, h5, h6 {
		margin: 16px 0;
	}
	img {
		width: 100%;
		max-width: 100%;
	}
	table {
		width: 100%;
		tbody {
			display: block;
			width: 100%;
			border: 1px solid var(--miscellaneous-color);
			background: #e5e5e5;
			border-radius: 5px;
		}
		tr {
			display: flex;
			width: 100%;
			border-bottom: 1px solid var(--miscellaneous-color);
			&:last-child {
				border-bottom: 0;
			}
			>* {
				padding: 10px 20px;
			}
			>*:nth-child(odd) {
				flex: 0 0 30%;
				min-width: 130px;
			}
			>*:nth-child(even) {
				flex: 1;
				font-weight: 600;
				background: var(--background-color);
			}
		}
	}
	@media (min-width: 1000px) {
		p {
			margin: 0 0 30px;
			font-size: 18px;
			span {
				font-size: 15px;
			}
		}
	}
}
.product__toggle-nav {
	display: inline-block;
	a {
		color: var(--text-color);
		font-weight: 500;
		line-height: 1.4em;
		font-size: 20px;
		position: relative;
		transition: 0.25s all ease-in-out;
	}
	&.active {
		a {
			color: var(--primary-color);
		}
	}
	&:hover {
		a {
			color: var(--primary-color);
		}
	}
	&.nav-mobile {
		display: block;
		margin: 20px 0;
		a {
			margin: 0;
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
		.expand {
			width: 24px;
			height: 24px;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: var(--border-radius);
			border: 1px solid var(--text-color);
			svg {
				width: 6px;
				height: 6px;
				stroke: var(--text-color);
			}
		}
		.state-default {
			display: block;
		}
		.state-active {
			display: none;
		}
		&.active {
			.state-default {
				display: none;
			}
			.state-active {
				display: block;
			}
			.expand {
				border-color: var(--primary-color);
				svg {
					stroke: var(--primary-color);
				}
			}
		}
	}
}
.product__toggle-item {
	.product__toggle-content {
		.btn {
			font-size: 15px;
		}
	}
}
.product-description-default {
	.product__toggle-item:not(:last-child) {
		border-bottom: 1px solid var(--miscellaneous-color);
	}
	.product__toggle-nav {
		pointer-events: none;
		margin: 20px 0;
	}
	.product__toggle-item {
		.product__toggle-content {
			height: auto;
			opacity: 1;
			visibility: visible;
		}
	}
	@media (min-width: 750px) {
		.product__toggle-item:not(:last-child) {
			padding-bottom: 30px;
			margin-bottom: 30px;
		}
		.product__toggle-nav {
			margin-top: 0;
		}
	}
}
@media (max-width: 749px) {
	.product__toggle-item {
		.product__toggle-content {
			height: 0;
			opacity: 0;
			visibility: hidden;
		}
		&.active {
			.product__toggle-content {
				height: auto;
				opacity: 1;
				visibility: visible;
			}
		}
	}
	.product-description-body {
		margin-top: 20px;
	}
	.product__toggle-content {
		>* {
			margin-bottom: 30px;
		}
	}
}
@media (min-width: 750px) {
	.product__toggle-nav {
		a {
			font-size: 25px;
			&:before {
				content: "";
				@include ab(n,0,0px,n);
				width: 100%;
				height: 2px;
				background: var(--primary-color);
				transition: transform 0.5s;
				transform: scaleX(0);
				transform-origin: right;
			}
		}
		&.active {
			a {
				&:before {
					transform: scaleX(1);
					transform-origin: left;
				}
			}
		}
		&:hover {
			a {
				&:before {
					transform: scaleX(1);
					transform-origin: left;
				}
			}
		}
		&.nav-mobile {
			display: none;
		}
	}
	.product-description-tab {
		.product__toggle-item {
			&:not(.active) {
				@include ab(0,0,n,n);
				width: 100%;
				visibility: hidden;
				opacity: 0;
			}
		}
	}
	.product-description-default {
		.product__toggle-item:not(:last-child) {
			margin-bottom: 60px;
			padding-bottom: 60px;
			border-bottom: 1px solid var(--miscellaneous-color);
		}
	}
}